* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
  background-color: rgb(236, 236, 236);
  font-family: iranYekan;
}

form {
  height: 100px;
  width: 100vw;
  display: flex;
}
input {
  flex: 3;
}

.backButton {
  background: purple;
  flex: 1;
}
.searchButton {
  flex: 1;
  background-color: green;
}


@keyframes my-animation {
  from {
    color: rgb(199, 199, 199);
    transform: translateY(50px)
  } to {
    color: rgb(48, 48, 48);
    transform: translateY(0)
  }
}
@keyframes my-animationOut {
  from {
    color: rgb(56, 56, 56);
    transform: translateY(0)
  } to {
    color: rgb(226, 226, 226);
    transform: translateY(-50px)
  }
}

.ProvList, .CityList, .DistList, .MatchedList {
  width: 95vw;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  align-items: center;
}



.province, .city, .district, .matched {
  width: 90%;
  height: 50px;
  border-bottom: 1px solid rgb(230, 230, 230);
  margin-bottom: 10px;
  text-align: right;
}
